import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { Accordion, AccordionItem, AccordionItemTitle, AccordionItemBody } from "carbon-components-react";

const dispatchedEvents = ["ABOUT#clicked"];

/*
    Example: <srs-accordion>
                <srs-accordion-item title="Item 1">
                    <p>Content of Item 1</p>
                </srs-accordion-item>
                <srs-accordion-item title="Item 2">
                    <p>Content of Item 2</p>
                </srs-accordion-item>
            </srs-accordion>
*/
const SrsAccordion = function (props, ref) {
    return (<Accordion><slot/></Accordion>);
}

const styles = `
    
`
const props = [{
    name: "open",
    defaultValue: "false",
    type: "boolean"
}]

const slots = [{name: "default", allowed: ["srs-accordion-item"]}]; 

const WrappedComponent = wrapper(React.forwardRef(SrsAccordion), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };